import { Injectable } from "@angular/core";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";
import {
  IViolationModel,
  IViolationToDisplayModel,
  IViolationRequest,
  IViolationAllType,
} from "../models/violations";
import { map } from "rxjs/operators";
import { db } from "app/shared/resources/database-tabels";
import { IPageParams } from "app/shared/interfaces/params";

@Injectable({
  providedIn: "root",
})
export class ViolationsService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IViolationRequest) {
    return this.post<IResponse<IViolationToDisplayModel>>(
      "Violation/Create",
      data
    );
  }

  update(data: IViolationRequest) {
    return this.post<IResponse<IViolationToDisplayModel>>(
      "Violation/Edit",
      data
    );
  }

  getList(params: IPageParams) {
    return this.get<IResponseList<IViolationModel[]>>("Violation/GetList", {
      params: {
        ...params,
        $expand: `${db.Att_Attendance_Element}.${db.Att_Violation}/${db.Att_Type},${db.Att_Attendance_Element}.${db.Att_Violation}/${db.Att_Violation_Type}`,
      },
    });
  }

  getAll() {
    return this.get<IResponse<IViolationModel[]>>("Violation/GetAll", {
      params: {
        $expand: `${db.Att_Attendance_Element}.${db.Att_Violation}`,
      },
    });
  }

  getById(violationId: number) {
    return this.get<IResponse<IViolationModel[]>>("Violation/GetAll", {
      params: {
        $expand: `${db.Att_Attendance_Element}.${db.Att_Violation}/${db.Att_Type},${db.Att_Attendance_Element}.${db.Att_Violation}/${db.Att_Violation_Type}`,
        $filter: `id eq ${violationId}`,
      },
    });
  }

  Delete(Id: number) {
    return this.delete<IResponse>("Violation/Delete", {
      params: { Id: Id },
    });
  }

  // minutes / day / month
  getAllTypes() {
    return this.get<IResponse<IViolationAllType[]>>(
      "Violation/GetAll_Type"
    ).pipe(
      map((res) => {
        res.data.map((item: IViolationAllType) => {
          item.ar_Name = item.en_Name;
        });
        return res;
      })
    );
  }

  exportExcel() {
    this.getAll();
  }
}
