import { NgModule } from "@angular/core";
import { RouterModule, Routes } from "@angular/router";
import { ViewsManager } from "app/shared/resources/views-manager";
import { AddVersionComponent } from "./components/version/add-version/add-version.component";
import { VersionIndexComponent } from "./components/version/version-index/version-index.component";
import { CheckIsViewAllowedGuard } from "app/shared/Guards/check-is-allowd.guard";
import { pathMatch } from "app/shared/enums/route-path-match.enum";
import { PenaltiesComponent } from "./components/penalties/penalties.component";
import { ViolationsIndexComponent } from "./components/violations/violations-index/violations-index.component";
import { AddViolationComponent } from "./components/violations/add-violation/add-violation.component";
import { PenaltyRegulationsIndexComponent } from "./components/penalty-regulations/penalty-regulations-index/penalty-regulations-index.component";
import { AddPenaltyRegulationsComponent } from "./components/penalty-regulations/add-penalty-regulations/add-penalty-regulations.component";
import { ViolationTypesComponent } from "./components/violation-types/violation-types.component";
import { VacationIndexComponent } from "./components/vacation/vacation-index/vacation-index.component";
import { VacationTypeComponent } from "./components/vacation/vacation-type/vacation-type.component";

const routes: Routes = [
  {
    path: "version",
    data: { view: ViewsManager.version },
    canActivate: [CheckIsViewAllowedGuard],
    children: [
      {
        path: "",
        redirectTo: "index",
        pathMatch: pathMatch.full,
      },
      {
        path: "index", // version/index
        component: VersionIndexComponent,
        data: { view: ViewsManager.version_index },
        canActivate: [CheckIsViewAllowedGuard],
      },
      {
        path: "add", // version/add
        component: AddVersionComponent,
        data: { view: ViewsManager.add_version },
        canActivate: [CheckIsViewAllowedGuard],
      },
      {
        path: "edit/:id", // version/edit/1
        component: AddVersionComponent,
        data: { view: ViewsManager.add_version },
        canActivate: [CheckIsViewAllowedGuard],
      },
    ],
  },
  {
    path: "localization-penalties",
    component: PenaltiesComponent,
    data: { view: ViewsManager.localization_penalties },
    canActivate: [CheckIsViewAllowedGuard],
  },
  {
    path: "localization-violation-types",
    component: ViolationTypesComponent,
    data: { view: ViewsManager.localization_violation_types },
    canActivate: [CheckIsViewAllowedGuard],
  },
  {
    path: "localization-violations",
    data: { view: ViewsManager.localization_violations },
    children: [
      {
        path: "",
        redirectTo: "index",
        pathMatch: pathMatch.full,
      },
      {
        path: "index",
        component: ViolationsIndexComponent,
        data: { view: ViewsManager.localization_violations_index },
        canActivate: [CheckIsViewAllowedGuard],
      },
      {
        path: "add",
        component: AddViolationComponent,
        data: { view: ViewsManager.localization_add_violation },
        canActivate: [CheckIsViewAllowedGuard],
      },
      {
        path: "edit/:violationId",
        component: AddViolationComponent,
        data: { view: ViewsManager.localization_add_violation },
        canActivate: [CheckIsViewAllowedGuard],
      },
    ],
  },
  {
    path: "localization-penalty-regulations",
    data: { view: ViewsManager.localization_penalty_regulations },
    children: [
      {
        path: "",
        redirectTo: "index",
        pathMatch: pathMatch.full,
      },
      {
        path: "index",
        component: PenaltyRegulationsIndexComponent,
        data: { view: ViewsManager.localization_penalty_regulations_index },
        canActivate: [CheckIsViewAllowedGuard],
      },
      {
        path: "add",
        component: AddPenaltyRegulationsComponent,
        data: { view: ViewsManager.localization_add_penalty_regulations },
        canActivate: [CheckIsViewAllowedGuard],
      },
      {
        path: "edit/:penaltyRegulationId",
        component: AddPenaltyRegulationsComponent,
        data: { view: ViewsManager.localization_add_penalty_regulations },
        canActivate: [CheckIsViewAllowedGuard],
      },
    ],
  },
  {
    path: "localization-vacations",
    children: [
      {
        path: "",
        redirectTo: "index",
        pathMatch: pathMatch.full,
      },
      {
        path: "index",
        component: VacationIndexComponent,
        data: { view: ViewsManager.localization_vacation_index },
        canActivate: [CheckIsViewAllowedGuard],
      },

      {
        path: "add",
        component: VacationTypeComponent,
        data: { view: ViewsManager.localization_add_vacation },
        canActivate: [CheckIsViewAllowedGuard],
      },
      {
        path: "edit/:vacationId",
        component: VacationTypeComponent,
        data: { view: ViewsManager.localization_add_vacation },
        canActivate: [CheckIsViewAllowedGuard],
      },
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class LocalizationSetupRoutingModule {}
