import {
  IVacationAgeModel,
  IVacationModel,
  IVacationResponse,
} from "../models/vacation";

export function vacationToMap(
  vacations: IVacationResponse[]
): IVacationModel[] {
  return vacations.map((vacation) => {
    return <IVacationModel>{
      ...vacation.att_Vacation,
      display_In_Result: vacation.paY_Variable?.display_In_Result,
      variableCode: vacation.paY_Variable?.code,
    };
  });
}

export function vacationAgeToMap(
  vacationAges: IVacationAgeModel[]
): IVacationAgeModel[] {
  return vacationAges.map((vacationAge) => {
    return <IVacationAgeModel>{
      ...vacationAge,
      From_Age: vacationAge.from_Age,
      To_Age: vacationAge.to_Age,
    };
  });
}
