import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";
import {
  IVacationInsuranceModel,
  IVacationInsuranceRequest,
} from "../../models/vacation";
import { IResponse } from "app/shared/interfaces/responces";
import { IPageParams } from "app/shared/interfaces/params";

@Injectable({
  providedIn: "root",
})
export class VacationInsuranceService extends AppClientService {
  constructor() {
    super();
  }
  create(data: IVacationInsuranceRequest) {
    return this.post<IResponse<IVacationInsuranceModel>>(
      "VacationTypeLoc/AddVacationInsuranceBased",
      data
    );
  }

  update(data: IVacationInsuranceRequest) {
    return this.post<IResponse<IVacationInsuranceModel>>(
      "VacationTypeLoc/EditVacationInsuranceBased",
      data
    );
  }

  getList(params: IPageParams, vacationId: number) {
    return this.get<IResponse<IVacationInsuranceModel[]>>(
      "VacationTypeLoc/GetVacationInsuranceBasedList",
      {
        params: { ...params, $filter: `vacation_ID eq ${vacationId}` },
      }
    );
  }

  Delete(id: number) {
    return this.delete<IResponse>(
      "VacationTypeLoc/DeleteVacationInsuranceBased",
      {
        params: { Id: id },
      }
    );
  }
}
