import { Injectable } from "@angular/core";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";
import { IVacationRequest, IVacationResponse } from "../../models/vacation";

@Injectable({
  providedIn: "root",
})
export class VacationService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IVacationRequest) {
    return this.post<IResponse<IVacationResponse>>(
      "VacationTypeLoc/Create",
      data
    );
  }

  update(data: IVacationRequest) {
    return this.post<IResponse<IVacationResponse>>(
      "VacationTypeLoc/Edit",
      data
    );
  }

  getList(params: IPageParams) {
    return this.get<IResponse<IVacationResponse[]>>("VacationTypeLoc/GetList", {
      params: params,
    });
  }

  getById(id: number) {
    return this.get<IResponse<IVacationResponse[]>>("VacationTypeLoc/GetAll", {
      params: {
        $filter: `id eq ${id}`,
      },
    });
  }

  getAll() {
    return this.get<IResponse<IVacationResponse[]>>("VacationTypeLoc/GetAll");
  }

  Delete(id: number) {
    return this.delete<IResponse>("VacationTypeLoc/Delete", {
      params: { Id: id },
    });
  }

  exportExcel() {
    this.getAll();
  }
}
